<template>
  <div id="app">
    <!-- img class="logo" src="./assets/logo2.png" -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
body, html{
  margin: 0
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ffffffdd;
  text-align: center;
  height: 100%;
  width: 100%;
  position: fixed;
  overflow-y: scroll;
  background-image: linear-gradient(to right, #702040 ,#001000);
}
#app.no-scroll{
  overflow-y: hidden;
}
a.email{
  color: #dd4444;
  color: white;
  border: 1px solid white;
  text-decoration: none;
  padding: 0.5vh 1.5vh;
  border-radius: 5px;
}

.logo{
  display:none;
}
h1,h2,h3,p{
  margin-top: 0;
  user-select: none;
  mix-blend-mode: difference;
}
.no-scroll h1, .no-scroll h2, .no-scroll h3, .no-scroll p{
  mix-blend-mode: unset;
}
li{
  mix-blend-mode: difference;
}
h1{
  margin: 8vh auto;
  font-size: 5vh;
  line-height: 1;
  padding: 2vh;
  background-color: #ffffff22;
  color: white;
  transform: skewY(-5.5deg);
}
/* INJECTED HTML*/
.modal700{
    width: 75%;
    display: block;
    margin: 30px auto 0;
    max-width: 700px;
    // box-shadow: 1px 2px 10px #500020c0;
}
@media only screen and (max-width : 600px) {
  .modal700{
    width: calc(100% - 30px);
    margin: 15px auto 0;
  }
}
.modal500{
    width: 75%;
    display: block;
    margin: 30px auto 0;
    max-width: 500px;
    // box-shadow: 1px 2px 10px #500020c0;
}
@media only screen and (max-width : 600px) {
  .modal500{
    width: calc(100% - 30px);
    margin: 15px auto 0;
  }
}
.modal-full{
    width: 100%;
    display: block;
    margin: 0 auto;
    margin-bottom: 3vh;
}
iframe{
    margin-bottom: 0;
}
iframe.youtube-video{
    width: 100%;
    max-width: 700px;
}
iframe.youtube-video-small{
    width: 100%;
    max-width: 560px;
}

.VueCarousel-dot-container{
  margin-top: -30px !important
}

#selector img{
  width: 60px;
  height: 60px;
  box-shadow: 0 3px 5px rgba(0,0,0,.4);
  margin: 2px;
  cursor:pointer;
  clip-path: polygon(0 5px, 100% 0, 100% calc(100% - 5px), 0 100%);
  transition: 0.25s;
  filter: grayscale(100%);
  opacity: 0.5
}
#selector img:hover{
  filter: grayscale(0);
  transform: translate3d(0,-5px, 0);
  opacity: 1;
}

.webvr-ui-title{
  font-size: 12px
}
a#magic-window{
  color: white !important;
  text-shadow: 0 0 10px black
}

</style>



